.Toolbar {
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #9E6F29;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 9000;

    nav {
        height: 100%;
    }
}

.Logo {
    height: 80%;
}

@media(max-width: 649px){
    .DesktopOnly {
        display: none;
    }
}